<template>
    <div class="withdrawal-list">
        <el-table
                :data="tableData.list"
                :empty-text="$L('暂无数据')"
                :header-cell-style="{background:'#f0f1f5',padding:'10px',color:'#666'}"
                style="width: 100%" class="table">
            <el-table-column
                    prop="dateTime"
                    :label="$L('时间')"
                    sortable
                    align="left">
            </el-table-column>
            <el-table-column
                    prop="price"
                    :label="$L('提现金额')"
                    sortable
                    align="center">
            </el-table-column>
            <el-table-column
                    prop="fee"
                    :label="$L('手续费')"
                    sortable
                    align="center">
            </el-table-column>
            <el-table-column
                    prop="realPrice"
                    :label="$L('实际到账金额')"
                    sortable
                    align="center">
            </el-table-column>
            <el-table-column
                    prop="deposType"
                    :label="$L('提现方式')"
                    align="center">
            </el-table-column>
            <el-table-column
                    :label="$L('收款账号')"
                    align="left">
                <div slot-scope="scope">
                    <span>{{ scope.row.toAccount ? scope.row.toAccount : $L('无')}}</span>
                </div>
            </el-table-column>
            <el-table-column
                    :label="$L('状态')"
                    align="center"
                    width="150">
                <div slot-scope="scope">
                    <p v-bind:class="statusForm(scope.row.status).color">{{ scope.row.status }}</p>
                </div>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "withdrawalList",
        props : {
            tableData : {
                type : Object,
                default : ()=>{
                    return {};
                }
            }
        },
        methods : {
            statusForm(value) {
                let x;
                switch (value) {
                    case "等待处理":
                        x = { color: 'font-green'};
                        break;
                    case "已结算":
                        x = { color: 'font-red'};
                        break;
                    default:
                        x = { color: 'font-grey'};
                        break;
                }
                return x;
            },
        }
    }
</script>

<style lang="scss" scoped>
	@import "../../../../assets/css/common/lib.scss";
    .withdrawal-list{
        padding: 20px;
        background: #fff;
        /deep/.table{
             .el-table__body-wrapper{
                border-left: 1px solid #ebeef5;
                border-right: 1px solid #ebeef5;
            }
			.el-table__header-wrapper .cell{
                font-weight: 800;
            }
            .font-red {
                color: #f00;
            }
            .font-grey {
                color: #999;
            }
            .font-green {
                color: #00bf00;
            }
        }
        .list-pages{
            text-align: center;
            padding: 40px 0 20px 20px;
            background: #fff;
        }
    }
</style>