<template>
    <div class="withdrawal">
            <div class="withdrawal-head" v-if="tableData && tableData.hasOwnProperty('statistics')">
                <div class="top">
                    <div class="left">
                        <span class="font-big">{{ $L('总佣金') }}：{{ statistics.total }}</span>
                        <span class="font-small">{{ $L('分销佣金') }}：{{ statistics.no_share_out_bonus }}</span>
                        <span class="font-small">{{ $L('分红金额') }}：{{ statistics.share_out_bonus }}</span>
                    </div>
                </div>
                <div class="main-box">
                    <div class="main-item">
                        <i class="bbx-font mainFontColor bbx-icon-weibiaoti--11"></i>
                        <div class="info">
                            <p class="p">{{ $L('待结算') }}</p>
                            <p class="font-large">{{ statistics.no_settlement }}</p>
                        </div>
                    </div>
                    <div class="main-item">
                        <i class="bbx-font mainFontColor bbx-icon-weibiaoti--12"></i>
                        <div class="info">
                            <p class="p">{{ $L('可提现') }}</p>
                            <p class="font-large">{{ statistics.withdrawal_allow }}</p>
                        </div>
                    </div>
                    <div class="main-item">
                        <i class="bbx-font mainFontColor bbx-icon-weibiaoti--13"></i>
                        <div class="info">
                            <p class="p">{{ $L('提现中') }}</p>
                            <p class="font-large">{{ statistics.withdrawal_conduct }}</p>
                        </div>
                    </div>
                    <div class="main-item">
                        <i class="bbx-font mainFontColor bbx-icon-weibiaoti--14"></i>
                        <div class="info">
                            <p class="p">{{ $L('已提现') }}</p>
                            <p class="font-large">{{ statistics.withdrawal_end }}</p>
                        </div>
                    </div>
                </div>
            </div>
            <div class="list" v-if="tableData && tableData.hasOwnProperty('list')">
                <div class="list-top">
                    <div class="system-header">
                        <ul class="system-header-list">
                            <li class="li mainBorderColor" @click="accountListFunc" :class="{currentClick:accountList}">{{$L(' 结算账户明细')}}</li>
                            <li class="li mainBorderColor" @click="withdrawalListFunc" :class="{currentClick:withdrawalList}">{{$L('提现记录')}}</li>
                        </ul>
                    </div>
                    <div class="right">
                        <span class="tips">{{ $L('显示时间') }}：</span>
                        <el-date-picker class="time-search"
                                        style="width: 350px;"
                                        v-model="SearchTimeZone"
                                        type="datetimerange"
                                        align="right"
                                        size="small"
                                        :start-placeholder="$L('开始日期')"
                                        :end-placeholder="$L('结束日期')"
                                        value-format="yyyy-MM-dd HH:mm:ss"
                                        :default-time="['00:00:00', '23:59:59']"
                                        @change="SearchTimeFunc">
                        </el-date-picker>
                        <el-select class="search" v-model="messageType" size="small"
                                   @change="changeTypeFunc" v-if="withdrawalList">
                            <el-option
                                    v-for="item in messageTypelist2"
                                    :key="item.type"
                                    :label="item.name"
                                    :value="item.type">
                            </el-option>
                        </el-select>
                    </div>
                </div>
                <account-list v-loading="loading" v-if="accountList" v-bind:tableData="tableData"></account-list>
                <withdrawal-list v-loading="loading" v-if="withdrawalList" v-bind:tableData="tableData"></withdrawal-list>
                <div class="list-pages" v-if="tableData.allNum>pageSize">
                    <el-pagination
                            :background="true"
                            @size-change="handdleSizeChange"
                            @current-change="handdleCurrentChange"
                            :current-page="currentPage"
                            :page-size="pageSize"
                            layout="prev, pager, next, jumper"
                            :total="tableData.allNum">
                    </el-pagination>
                </div>
            </div>
        </div>
</template>

<script>
    import AccountList from "./withdrawalChildren/accountList";
    import WithdrawalList from "./withdrawalChildren/withdrawalList";

    export default {
        name: "withdrawal",
        data (){
            return {
                loading : false,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                tableData: {},
                statistics:{},
                messageTypelist:[
                    {
                        type:-999,
                        name:this.$L('全部状态')
                    },
                    {
                        type:2,
                        name:this.$L('待结算')
                    },
                    {
                        type:1,
                        name:this.$L('已结算')
                    },
                    {
                        type:3,
                        name:this.$L('等待处理')
                    },
                    {
                        type:4,
                        name:this.$L('已发放')
                    }, {
                        type:5,
                        name:this.$L('已拒绝')
                    }
                ],
                messageType:-999,
                messageTypelist2:[
                    {
                        type:-999,
                        name:this.$L('全部状态')
                    },
                    {
                        type:0,
                        name:this.$L('待处理')
                    },
                    {
                        type:1,
                        name:this.$L('已发放')
                    },
                    {
                        type:-1,
                        name:this.$L('已拒绝')
                    }
                ],
                SearchTimeZone:'',
                selectType:'account',
                currentPage:1,
                pageSize:20,
                accountList: true,
                withdrawalList: false,
                dtCreateStart: '',
                dtCreateEnd: '',
            }
        },
        components : {
            WithdrawalList,
            AccountList,
        },
        created () {
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p: _self.currentPage,
                    num: _self.pageSize,
                    isMobile:0,
                    selectType:_self.selectType,
                    status: _self.messageType,
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd
                };
                _self.$api('distributionMyCommission', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.statistics =  reData.data.statistics;

                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            SearchTimeFunc(time) {
                if (time !== null) {
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                } else {
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }
                this.getData();
            },
            handdleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            handdleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            accountListFunc(){
                this.accountList = true;
                this.withdrawalList = false;
                this.selectType = "account";
                this.currentPage = 1;
                this.getData();
            },
            withdrawalListFunc(){
                this.withdrawalList = true;
                this.accountList = false;
                this.selectType = "withdraw";
                this.currentPage = 1;
                this.getData();
            },
            changeTypeFunc(val){
                this.type = val;
                this.getData();
            },

        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common.scss";
    @import "../../../assets/css/common/lib.scss";
    .withdrawal{
        position: relative;
        padding: 20px!important;
        .withdrawal-head{
            position: relative;
            padding: 10px 20px;
            background: #fff;
            .top{
                height: 30px;
                .left{
                    display: inline-block;
                    height: 30px;
                    line-height: 30px;
                    float: left;
                    .font-big{
                        font-size: 18px;
                    }
                    .font-small{
                        padding-left: 40px;
                        font-size: 14px;
                    }
                }
                .right{
                    display: inline-block;
                    height: 30px;
                    line-height: 30px;
                    float: right;
                    cursor: pointer;
                }
            }
            .main-box{
                padding: 40px 0;
                display: flex;
                justify-content: flex-start;
                .main-item{
                    width: 25%;
                    .bbx-font{
                        font-size: 56px;
                        vertical-align: super;
                        margin-right: 20px;
                    }
                    .info{
                        display: inline-block;
                        overflow: hidden;
                        width: calc(100% - 80px);
                        text-overflow: ellipsis;
                        white-space: inherit;
                        .p {
                            padding-left: 15px;
                            font-size: 14px;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: inherit;
                        }
                        .font-large{
                            font-size: 26px;
                            font-weight: 700;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: inherit;
                        }
                    }
                }
            }
        }
        .list{
            margin-top: 20px;
            .list-top{
                height: 60px;
                .system-header{
                    height: 60px;
                    float: left;
                    .system-header-list{
                        @extend .no-select;
                        .li{
                            min-width: 120px;
                            height: inherit;
                            line-height: 60px;
                            display: inline-block;
                            text-align: center;
                            font-size: 14px;
                            background: #dfdfdf;
                            border-color: #f8f8f9;
                            cursor: pointer;
                        }
                        .currentClick {
                            background-color: #fff;
                        }
                    }

                }
                .right{
                    float: right;
                    .tips{
                        display: inline-block;
                        height: 32px;
                        line-height: 32px;
                    }
                    .time-search{
                        vertical-align: bottom;
                    }
                    .font{
                        padding-left: 20px;
                        font-size: 16px;
                    }
                    .search{
                        width: 120px;
                        padding-left: 10px;
                    }
                    .input-with-select{
                        width: calc(100% - 480px);
                        padding-left: 10px;
                        /deep/ .el-input-group__append{
                            font-size: 14px;
                            color: #333;
                            padding: 0 10px;
                            background: #fff;
                            border-left-color: #fff;
                        }
                    }
                }
            }
            .list-pages{
                text-align: center;
                padding: 20px 0 40px;
                background: #fff;
            }
            .currentClick {
                background-color: #fff;
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }

    @media screen and (max-width: 1440px) {
        .main-box{
            .main-item{
                margin-right: 5%!important;
            }
        }
    }
</style>