<template>
    <div class="account-list">
        <el-table
                :data="tableData.list"
                :empty-text="$L('暂无数据')"
                :header-cell-style="{background:'#f0f1f5',padding:'10px',color:'#666'}"
                style="width: 100%" class="table">
            <el-table-column
                    prop="createTime"
                    :label="$L('时间')"
                    align="left">
            </el-table-column>
            <el-table-column
                    prop="bill"
                    :label="$L('出入账')"
                    align="center">
            </el-table-column>
            <el-table-column
                    :label="$L('详细')"
                    align="left">
                <div slot-scope="scope">
                    {{ scope.row.describe }}<br>
                    <span class="mainFontColor">{{ $L('订单编号') }}：{{ scope.row.describe_number }}</span>
                </div>
            </el-table-column>
            <el-table-column
                    prop="amount"
                    :label="$L('金额')"
                    align="center">
            </el-table-column>
            <el-table-column
                    :label="$L('状态')"
                    align="center">
                <div slot-scope="scope">
                    <p v-bind:class="statusForm(scope.row.status).color">{{ scope.row.status }}</p>
                </div>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "accountList",
        data () {
            return {

            }
        },
        props : {
            tableData : {
                type : Object,
                default : ()=>{
                    return {};
                }
            }
        },
        methods : {
            statusForm(value) {
                let x;
                switch (value) {
                    case "已结算":
                        x = { color: 'font-green'};
                        break;
                    default:
                        x = { color: 'font-red'};
                        break;
                }
                return x;
            },
        }
    }
</script>

<style lang="scss" scoped>
	@import "../../../../assets/css/common/lib.scss";
    .account-list{
        padding: 20px;
        background: #fff;
        /deep/ .table{
            .el-table__body-wrapper{
                border-left: 1px solid #ebeef5;
                border-right: 1px solid #ebeef5;
            }
             .el-table__header-wrapper .cell{
                font-weight: 800;
            }
            .font-red {
                color: #f00;
            }
            .font-grey {
                color: #999;
            }
            .font-green {
                color: #00bf00;
            }
        }
        .list-pages{
            text-align: center;
            padding: 40px 0 20px 20px;
            background: #fff;
        }
    }
</style>